/*---------- Content style  ----------*/

body {
  display: -ms-flexbox;
  display: flex;
  min-height: 700px;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #5b00a5;
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 30s;
          animation-duration: 30s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  background-image: url("https://www.graphicsfuel.com/wp-content/uploads/2012/11/magic-wand-icon-512.png");
  background-repeat: no-repeat;
  background-size: 20%;
  background-position: right bottom;
}

@-webkit-keyframes example {
    0%   {background-color:#5b00a5;}
    33%  {background-color:#000ead;}
    66%  {background-color:#5b00a5;}
    100%  {background-color:#000ead;}
}

@keyframes example {
    0%   {background-color:#5b00a5;}
    33%  {background-color:#000ead;}
    66%  {background-color:#5b00a5;}
    100%  {background-color:#000ead;}
}

.wholeThing {
  width: 500px;
  padding: 30px;
  margin-top: 30px;
  background-color: lavender;
  -webkit-box-shadow: 10px 10px 32px 3px rgba(0,0,0,0.3);
  box-shadow: 10px 10px 32px 3px rgba(0,0,0,0.3);
}

h2 {
  font-family: 'Bungee Inline', cursive;
}

li,
input,
button {
  font-family: 'Alegreya Sans', sans-serif;
  font-size: 1.2rem;
}

/*---------- List style ----------*/
li {
  list-style-type: none;
}

li:hover {
  color: darkblue;
  background-color: lightblue;
}

ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: end;
      align-items: flex-end;
}

button {
  margin: 5px;
}

button:focus {
  outline: none;
}

button:hover {
  background-color: white;
  color: darkblue;
}



